@import '../../Assets/Scss/colors'; 

.tableCard
{
    width: 100%;
    padding: 18px;
    box-shadow: none;
    cursor: pointer;
}

thead{
    color : $para-font;
    font-size: 12px;
}

thead:nth-child(3){
    display:none;
}

tbody tr td{
    font-weight: 400;
    font-size: 12px;
}

thead tr {
    border-bottom: 1.5px solid rgb(226, 226, 226);
}
thead tr th{    
    font-size: 11px;
}



ul[data-test="pagination"]{
    margin: 0px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

ul[data-test="pagination"] a{
    background-color : $main-theme;
    color:$hover-font;
}

li[data-test="page-item"]{
    padding-left : 10px;

}

li[data-test="page-item"]:hover{
    color: $main-theme;
    
}

li[data-test="page-item"] a:hover{
    border: 1px solid $main-theme;
}

li[data-test="page-item"] span{
    font-size:18px;
}


div[data-test="datatable-pagination"]{
    position: absolute;
    bottom: -65px;
    right: -5px;
}

div[data-test="datatable-info"]{
    // display: flex;
    // align-items: center;
    color: $para-font;
    font-size:15px; 
    position: absolute;
    bottom: -46px;
    left: -5px;
}


tbody tr td:nth-child(7){
    color : $para-font;
    font-weight:800;
    font-size:20px;
}


