@import './Assets/Scss/colors';
@import './Assets/Scss/spacing';

*{
  box-sizing: border-box;
}

html{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    background-color: $main-bg !important;
    margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-wrapper{
  border-radius: 15px;
  padding: 20px;
  background-color: $card-bg;
}

@media (max-width:767px){
  .card-wrapper{
    margin-bottom: 15px;
  }
}

.full-view {
  height: 100vh;
}

.carousel .slider-wrapper{
  padding: 25px;
  height: 100%;
}

.carousel.carousel-slider{
  height: 30vh;
}
.submitFormButton{
  cursor: pointer !important;
}

.submitFormButtonDisabled{
  cursor: not-allowed !important;
}

.greenBg {
  background-image: url('../src/Assets/CardsBg/bg-green.svg');
}

.greenWBg {
  background-image: url('../src/Assets/CardsBg/bg-w-green.svg');
}


.blueBg{
  background-image: url('../src/Assets/CardsBg/bg-blue.svg');
}

.redBg{
  background-image: url('../src/Assets/CardsBg/bg-red.svg');
}

.yellowBg{
  background-image: url('../src/Assets/CardsBg/bg-yellow.svg');
}

.greenbackg{
  background-color: $main-theme;
}

.yellowbackg{
  background-color: $yellow;
}

.bluebackg{
  background-color: $para-font;
}

.redbackg{
  background-color: $danger-theme;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.react-multi-carousel-list{
  padding-bottom: 20px;
  padding-top: 10px;
  :hover{
      cursor: pointer;
  }

  ul{
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-around;
      height: 150px;
  }
  
  li{
      transition: 0.2s;
      display: flex;
      justify-content: center;
  }

  ul li:hover{
      transform: scale(1.1);
  }
}
.pagination .page-item.active .page-link{
    color: $card-bg;
    background-color : $main-theme;
}

.pagination .page-item.active .page-link:hover{
  color: $main-theme;
}

.table{
  margin-bottom: 0px !important;
}


.wrapper-padding{
  padding-bottom: 20px;
}



//Notification 

.notifications .icon div.count{
  color: $main-theme;
  background-color: $main-theme;
  height: 8px;
  width: 8px;
  max-width: 8px;
  top: 0px;
  left: 23px;
  min-height: 8px;
}

.notifications .icon img.image {
  height: 21px ;
  width: 20px;
  position: absolute;
  top: 5px;
  left: 5px;
}

.notifications div.container{
  width: 360px;
  border: none;
  border-radius: 15px;
  top: 65px;
  height: 260px;
}

.notifications .container .header div.header-option{
  color: $main-theme;
}

.notifications .container div.items {
  height: 200px;
  overflow-y: hidden;

  div.card{
    border: none;
    box-shadow: none;
  }
}

@media (max-width:992px){
  .notifications div.container{
    width: 278px;
  }
}
